.sidebar {
    width: 100%;
    height: 100%;
    padding: .5rem;
    will-change: height;
    color: $main_fontColor;
    opacity: 1;
    pointer-events: all;
    display: block;
    @media (max-width: $tablet) {
        display: none; }
    @media (min-width: $disktop) {
        display: block; }
    .time-option {
        padding: 1rem;
        color: #000;
        background-color: #ebf1f9;
        display: flex;
        align-items: center;
        border-radius: 3px;
        i {
            font-size: 30px; } }
    .desc {
        margin: 0rem 1rem; }
    .switch {
        position: relative;
        display: inline-block;
        width: 48px;
        height: 21px;
        margin-left: auto;
        .slider {
            position: absolute;
            cursor: pointer;
            top: 0%;
            left: 0%;
            right: 0%;
            bottom: 0%;
            background-color: #ccc;
            border-radius: 35px;
            @include mainTransition;
            &::before {
                content: "";
                position: absolute;
                height: 27px;
                width: 27px;
                left: 0px;
                bottom: -2px;
                background-color: #777777;
                border-radius: 50px;
                @include mainTransition; } }
        input {
            display: none; }
        input:checked + .slider {
            background-color: #6488c0; }
        input:checked + .slider:before {
            transform: translateX(21px);
            background-color: #3665ac; } }
    .filter-your-result {
        .main-header {
            margin: 2rem 0rem;
            position: relative;
            h2 {
                font-size: 20px;
                font-weight: 500; }
            span {
                color: #3665ac; } }
        .section-header {
            position: relative;
            h2 {
                font-size: 21px;
                margin: 1rem 0rem; }
            i {
                position: absolute;
                right: 0%;
                top: 38%;
                color: #3665ac;
                cursor: pointer;
                @include mainTransition;
                &.down {
                    transform: rotate(180deg); } } }
        .stops-section {
            .options-list {
                .form-check {
                    margin-bottom: 1rem;
                    .form-check-input {
                        margin: 0rem;
                        top: 10%;
                        left: 0%;
                        height: 18px;
                        width: 18px; } } }
            hr {
                margin: 2rem 0rem; } }
        .airelines-section {
            .options-list {
                .form-check {
                    margin-bottom: 1rem;
                    input {
                        margin: 0rem;
                        top: 10%;
                        left: 0%;
                        height: 18px;
                        width: 18px; } } }
            hr {
                margin: 2rem 0rem; } }
        .time-section {
            .options-list {
                h2 {
                    color: #6488c0;
                    font-size: 14px;
                    font-weight: 600;
                    margin: 1rem 0rem; }
                .form-check {
                    margin-bottom: 1rem;
                    .form-check-input {
                        margin: 0rem;
                        top: 10%;
                        left: 0%;
                        height: 18px;
                        width: 18px; } } }
            hr {
                margin: 2rem 0rem; } }
        .price-section {
            .options-list {
                .range {
                    margin-top: 1rem;
                    .field {
                        input {
                            -webkit-appearance: none;
                            height: 6px;
                            width: 100%;
                            background-color: #3665ac;
                            border-radius: 5px;
                            outline: none;
                            border: none;
                            margin: 0rem 0rem 1rem;
                            padding: 0rem; }
                        input::-webkit-slider-thumb {
                            -webkit-appearance: none;
                            width: 35px;
                            height: 35px;
                            background-color: #fff;
                            border: 1px solid #878080;
                            border-radius: 50%; }
                        .value {
                            display: inline-block;
                            font-size: 17px;
                            &.right {
                                float: right; } } } } }
            hr {
                margin: 2rem 0rem; } }
        .airports-section {
            .options-list {
                h2 {
                    color: #6488c0;
                    font-size: 14px;
                    font-weight: 600;
                    margin: 2rem 0rem; }
                .form-check {
                    margin-bottom: 1rem;
                    .form-check-input {
                        margin: 0rem;
                        top: 10%;
                        left: 0%;
                        height: 18px;
                        width: 18px; } } }
            hr {
                margin: 2rem 0rem; } }
        .baggage-section {
            .form-check {
                margin-bottom: 1rem;
                .form-check-input {
                    margin: 0rem;
                    top: 10%;
                    left: 0%;
                    height: 18px;
                    width: 18px; } }
            hr {
                margin: 2rem 0rem; } }
        .options-list {
            #reset-btn {
                input {
                    display: none; } } } } }
