    z-index: 99999;
    opacity: 0;
    pointer-events: none;
    padding: 1rem 0rem;
    @include mainTransition;
    &.active {
        opacity: 1;
        pointer-events: all; }
    .flightDetail-menu {
        .con-btn {
            transform: translate(0%, 0%);
