#scrollTop {
    position: fixed;
    right: 10px;
    bottom: 10px;
    background-color: #00000061;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    z-index: 10000;
    text-align: center;
    font-size: 18px;
    line-height: 36px;
    opacity: 0;
    pointer-events: none;
    cursor: pointer;
    @include mainTransition;
    @media (max-width: $tablet) {
        display: none; }
    &.show {
        opacity: 1;
        pointer-events: all;
        background-color: $second_color; }
    i {
        font-weight: 600;
        color: #fff;
        @include mainTransition; } }
