#preloader {
    position: fixed;
    z-index: 999999999 !important;
    background-color: $main_bgColor;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center; }
#preloader [data-loader=dual-ring] {
    width: 50px;
    height: 50px;
    content: " ";
    display: block;
    border-radius: 50%;
    border: 5px solid $preloader_color;
    border-color: $preloader_color transparent $preloader_color transparent;
    animation: dual-ring 1.2s ease-in-out infinite; }
@keyframes dual-ring {
    form {
        transform: rotate(0deg); }
    to {
        transform: rotate(360deg); } }
