header {
    background-color: $header_bgColor;
    background-color: #fff;
    &.mobile-view {
        padding: 15px;
        position: fixed;
        width: 100%;
        height: 100%;
        z-index: 999;
        overflow-x: scroll; }
    .navbar {
        display: grid;
        align-items: center;
        justify-items: center;
        align-items: center;
        grid-template-columns: 18% 1fr;
        padding: 0rem;
        position: relative;
        @media (max-width: $tablet) {
            display: none; }
        @media (min-width: $disktop) {
            display: grid; }
        &.mobile-view {
            display: flex;
            justify-content: center;
            align-items: center;
            .logo {
                order: 2; }
            .list-item {
                display: none; }
            .humburger-btn {
                display: block !important; } }
        .humburger-btn {
            background-color: transparent;
            height: auto;
            padding: 2px 7px;
            border: none;
            align-items: center;
            border-radius: 3px;
            outline: none;
            display: none;
            order: 1;
            position: absolute;
            top: 0%;
            left: 0%;
            bottom: 0%;
            svg {
                color: #000;
                width: 30px; } }
        .back-bttn {
            position: absolute;
            right: 0%;
            top: 25%;
            background-color: transparent;
            color: #000;
            border: none;
            height: auto;
            width: auto;
            padding: 6px 12px;
            font-size: 14px;
            outline: none;
            display: none;
            &.active {
                display: block; } }
        .logo {
            text-align: center;
            display: flex;
            align-items: center;
            font-size: 3rem;
            @media (max-width: $tablet) {
                font-size: 2.7rem; } }
        .list-item {
            color: $header_color;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            .item {
                position: relative;
                height: 100%;
                align-items: center;
                padding: 1rem 1rem;
                font-size: 13px;
                cursor: pointer;
                display: inline-block;
                @include mainTransition;
                @media (min-width: $LGdesktop) {
                    font-size: 20px; }
                i {
                    position: absolute;
                    bottom: 38%;
                    right: 0%;
                    font-size: 13px; }
                .contact-us {
                    text-decoration: none;
                    color: $header_color;
                    @include mainTransition;
                    &:hover {
                        color: $second_color; } }
                button {
                    background-color: $signin_color;
                    color: $main_color;
                    border: none;
                    height: auto;
                    width: auto;
                    padding: 5px 30px;
                    font-size: 13px;
                    outline: none;
                    border-radius: 4px;
                    cursor: pointer;
                    @include mainTransition;
                    @media ( min-width: $tablet ) {
                        font-size: 13px; }
                    &:hover {
                        background-color: $signin_Hovercolor; } }
                .dropdown-menu {
                    background-color: $preloader_color;
                    position: absolute;
                    top: 84%;
                    left: 24%;
                    width: 122px;
                    padding: 13px 8px 21px 12px;
                    font-size: 13px;
                    border-radius: 3px;
                    display: none;
                    a {
                        color: $main_color;
                        text-decoration: none; }
                    &::before {
                        content: "";
                        top: -20%;
                        left: 13px;
                        position: absolute;
                        border: 10px solid;
                        border-color: transparent transparent $second_color transparent; }
                    li {
                        margin: 6px 0rem 0px 0px; } }
                &:hover {
                    color: $second_color; }
                &.signIn-btn {
                    &:hover {
                        background-color: $header_bgColor; } }
                &.active {
                    color: $second_color;
                    .dropdown-menu {
                        display: block; } } } } }
