        @media (min-width: $phone) {
            width: 100%; }
        @media (min-width: $tablet) {
            width: 60%; }
        @media (min-width: $disktop) {
            width: 30%; }
        .header {
            a {
                text-decoration: none;
                color: $header_color; }
            i {
                float: right;
                cursor: pointer;
                font-size: 20px; } }
        .info {
            background: linear-gradient(-290deg, rgb(17, 85, 139) -46%, rgb(54, 101, 172) -14%, rgb(89, 146, 232) 99%);
            color: $main_color;
            padding: 1rem;
            margin-top: 1rem;
            border-radius: 5px;
            h2 {
                font-size: 22px;
                font-weight: 600; } }
        .box {
            margin-top: 2rem;
            h2 {
                text-align: center;
                font-size: 22px; }
            .facebook {
                display: block;
                width: 100%;
                background-color: #192a56;
                color: #fff;
                text-align: center;
                padding: .5rem 0rem;
                font-size: 18px;
                border-radius: 2px;
                margin: 2rem 0rem;
                text-decoration: none; }
            .or {
                position: relative;
                hr {
                    border-top: 2px solid rgba(0,0,0,0.1); }
                span {
                    color: #0000005c;
                    position: absolute;
                    bottom: -0.5rem;
                    right: 48%;
                    background-color: #fff; } }
            .options {
                position: relative;
                li {
                    display: inline-block;
                    cursor: pointer;
                    width: 49%;
                    text-align: center;
                    padding: 1rem 0rem;
                    i {
                        padding-right: 1rem; }
                    &.active {
                        border-bottom: 3px solid #192a56; } }
                hr {
                    position: absolute;
                    width: 100%;
                    top: 68%;
                    border-top: 3px solid #00000026; } }
            form {
                margin-top: 2rem;
                label {
                    margin-top: 2rem; }
                input {
                    display: block;
                    width: 100%;
                    outline: #192a56; }
                button {
                    display: block;
                    width: 100%;
                    margin: 2rem 0rem;
                    border: 1px solid $second_color;
                    outline: none;
                    background-color: $second_color;
                    color: $main_color;
                    padding: 1rem 0rem 2rem;
                    border-radius: 2px;
                    cursor: pointer; }
                .mobile-number {
                    .iti--allow-dropdown {
                        display: block;
                        width: 100%; } }
                .email-addres,.mobile-number {
                    display: none;
                    &.active {
                        display: block; } } }
            .forgot-password {
                display: block;
                width: 100%;
                text-align: center;
                color: #192a56; }
            .dont-haveAccount {
                margin: 2rem 0rem;
                a {
                    margin-left: 8px;
                    text-decoration: none;
                    color: #192a56; } } } }
    &.active {
        opacity: 1;
        pointer-events: all;
        .toggle-menu {
            transform: translateX(0%);
            pointer-events: all; } } }
