footer {
    padding: 1rem 2rem 0rem;
    display: block;
    @media (max-width: $tablet) {
        display: none; }
    @media (min-width: $LGdesktop) {
        padding: 1rem 2rem 0rem; }
    .container-fluid {
        border-top: 1px solid #a2a2a2; }
    .call-us-section,.copyright,.payment-section {
        display: flex;
        height: 100%;
        align-items: center;
        color: #707070;
        a {
            text-decoration: none;
            cursor: pointer;
            color: $second_color; } }
    .call-us-section {
        justify-content: flex-start;
        @media (max-width: $phone) {
            justify-content: center;
            font-size: 11px; }
        @media (min-width: $phone) {
            justify-content: center;
            font-size: 11px; }
        @media (min-width: $Stablet) {
            justify-content: center;
            font-size: 14px; }
        @media (min-width: $tablet) {
            justify-content: flex-start;
            font-size: 10px; }
        @media (min-width: $disktop) {
            font-size: 15px; }
        img {
            width: 15%;
            @media (max-width: $phone) {
                width: 10%; }
            @media (min-width: $phone) {
                width: 10%; }
            @media (min-width: $Stablet) {
                width: 20%; }
            @media (min-width: $tablet) {
                width: 15%; } }
        span {
            color: #707070; } }
    .copyright {
        justify-content: center;
        @media (max-width: $phone) {
            font-size: 11px; }
        @media (min-width: $phone) {
            font-size: 11px;
            justify-content: center; }
        @media (min-width: $tablet) {
            font-size: 9px; }
        @media (min-width: $disktop) {
            font-size: 15px; }
        @media (min-width: $LGdesktop) {
            justify-content: center; }
        p {
            margin: 0rem; } }
    .payment-section {
        @media (max-width: $phone) {
            justify-content: center;
            margin-top: 10px; }
        @media (min-width: $phone) {
            justify-content: center;
            margin-top: 10px; }
        @media (min-width: $Stablet) {
            justify-content: center; }
        @media (min-width: $tablet) {
            justify-content: flex-end;
            margin: 0rem; }
        @media (min-width: $LGdesktop) {
            justify-content: flex-end; }
        ul {
            li {
                display: inline-block;
                @media (min-width: $tablet) {
                    width: 18%; }
                img {
                    @media (max-width: $phone) {
                        width: 50%; }
                    @media (min-width: $phone) {
                        width: 50%; }
                    @media (min-width: $Stablet) {
                        width: 40%; }
                    @media (min-width: $tablet) {
                        width: 30%; }
                    @media (min-width: $disktop) {
                        width: 60%; }
                    @media (min-width: $LGdesktop) {
                        width: 80%; } } } } } }
