* {
    margin: 0rem;
    padding: 0rem;
    box-sizing: border-box; }
body {
    background-color: $body_bgColor;
    font-family: 'Poppins', sans-serif !important;
    padding: 0rem !important;
    margin: 0rem !important;
    overflow-x: hidden;
    &.no-scroll {
        overflow: hidden; }
    @media (max-width: $tablet) {
        background-color: #e8e8e87d; } }
ul,ol {
    list-style: none;
    margin: 0rem; }
.black {
    color: #000 !important; }
.border-l-none {
    border-left: none !important; }
#main-content {
    margin-top: 6%; }
.content {
    .container-fluid {
        @media (max-width: $tablet) {
            padding: 0rem; } }
    .window {
        display: flex;
        justify-content: center;
        .sidebar-window {
            @media (max-width: $Stablet) {
                display: none; }
            @media (min-width: $disktop) {
                width: auto; } }
        .flight-info-window {
            width: 100%;
            @media (min-width: $disktop) {
                .flightinfo {
                    flex: 1; } }
            @media (max-width: $tablet) {
                .flightinfo {
                    padding: 0rem; } } } } }
//-slelect flight page
.select-flight-page {
    .container-fluid {
        padding: 0rem 4rem;
        @media (max-width: $tablet) {
            padding: 0rem; } } }
.final-step-page {
    .container-fluid {
        padding: 0rem 4rem;
        @media (max-width: $tablet) {
            padding: 0rem; } } }
