.three-steps-valdiation {
    display: flex;
    align-items: center;
    @media (max-width: $tablet) {
        padding: 10px 20px;
        background-color: #fff; }
    .step {
        display: flex;
        align-items: center;
        .step_number {
            display: flex;
            flex-shrink: 0;
            padding-right: 8px;
            color: #3665ac;
            svg {
                width: 20px;
                height: 20px;
                display: block;
                fill: currentColor;
                display: inline-block;
                font-size: 1.5rem;
                transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
                flex-shrink: 0;
                user-select: none; }
            .svg-text {
                fill: #fff;
                font-size: 0.75rem; } }
        .title {
            width: 100%;
            font-size: 0.75rem; }
        &:nth-child(1) {
            .title {
                color: #ccc; }
            .step_number {
                padding: 1px 9px; } }
        &:nth-child(3) {
            .title {
                color: #000; } }
        &:nth-child(5) {
            .title {
                color: #ccc; }
            .step_number {
                color: #9b9999; } } }
    .line {
        border-bottom: 1px dashed #cccccc;
        flex: 1 1 auto;
        display: block; }
    .disktop-view {
        display: flex;
        align-items: center;
        flex-direction: row;
        flex: 1;
        margin: 2rem 0rem;
        @media (max-width: $tablet) {
            display: none; } }
    .mobile-view {
        display: none;
        flex: 1;
        align-items: center;
        flex-direction: row;
        background-color: #fff;
        margin: 0rem;
        @media (max-width: $tablet) {
            display: flex; }
        .line {
            border-bottom: 1px solid #cccccc; } }
    .final-step-mobile-view {
        display: none; } }
.final-step-page {
    .three-steps-valdiation {
        @media (max-width: $tablet) {
            box-shadow: 0px 9px 7px 1px #dedede;
            .disktop-view, .mobile-view {
                display: none; }
            .final-step-mobile-view {
                display: flex; } }
        .step {
            &:nth-child(1) {
                .title {
                    color: #ccc; } }
            &:nth-child(3) {
                .title {
                    color: #ccc; }
                .step_number {
                    color: #3665ac; } }
            &:nth-child(5) {
                .title {
                    color: #000; }
                .step_number {
                    color: #3665ac; } } }
        .final-step-mobile-view {
            flex: 1;
            justify-content: space-between;
            align-items: center;
            flex-direction: row;
            padding: 5px;
            .total-price {
                font-size: 18px;
                flex: 1;
                color: #000;
                display: flex;
                flex-direction: column;
                align-content: center;
                justify-content: flex-start;
                column-gap: 6px;
                .title {
                    color: #bbbbbb; }
                .price {
                    font-weight: 600; } }
            .view-booking-btn {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                flex: 1;
                button {
                    background-color: transparent;
                    color: #3665ac;
                    font-weight: 600;
                    font-size: 18px;
                    border: none;
                    width: auto;
                    height: auto;
                    padding: 0rem;
                    outline: none;
                    cursor: pointer; } } } } }
